import { createStore } from 'vuex'

export default createStore({
  state: {
		cartLists:[
			{id:'101',name:'苹果',desc:'陕西红富士',price:10,nums:1},
			{id:'102',name:'苹果',desc:'陕西红富士',price:10,nums:1},
			{id:'103',name:'苹果',desc:'陕西红富士',price:10,nums:1},
			{id:'104',name:'苹果',desc:'陕西红富士',price:10,nums:1},
		],
		orderLists:[
			{id:'101',name:'苹果',desc:'陕西红富士',price:10,nums:1,isdelivery:false},
			{id:'102',name:'苹果',desc:'陕西红富士',price:10,nums:1,isdelivery:false},
			{id:'103',name:'苹果',desc:'陕西红富士',price:10,nums:1,isdelivery:false},
		],
  },
	getters: { /* 通过this.$store.getters.getlist 调用 */
		getCartLists: function(state) {
			return state.cartLists;
		},
		getOrderLists:function(state){
			return state.orderLists;
		}
	},
  mutations: {
		addCartNum: function(state, data) {
			state.cartLists[data.index].nums++;
		},
		reduceCartNum: function(state, data) {
			if(state.cartLists[data.index].nums>1){
				state.cartLists[data.index].nums--;
			}
		},
		delCartGood:function(state,data){
			state.cartLists.splice(data.index,1);
		},
		addCart:function(state,data){
			let res = state.cartLists.findIndex((ele,index)=>{
				return ele.id==data.id;
			});
			if(res==-1){
				state.cartLists.push(data);
			}else{
				state.cartLists[res].nums++;
			}
		},
		cancelOrder: function(state, data) {
			state.orderLists.splice(data.index,1);
		},
		sureOrder:function(state,data){
			state.orderLists[data.index].isdelivery=true;
		},
  },
  actions: {
		cmAddCartNum: function({commit}, data) {
			commit('addCartNum', data);
		},
		cmReduceCartNum: function({commit}, data) {
			commit('reduceCartNum', data);
		},
		cmDelCartGood:function({commit},data) {
			commit('delCartGood',data);
		},
		cmAddCart:function({commit},data) {
			commit('addCart',data);
		},
		cmCancelOrder: function({commit}, data) {
			commit('cancelOrder', data);
		},
		cmSureOrder:function({commit},data) {
			commit('sureOrder',data);
		},
  },
  modules: {
  }
})
